





















import Vue from 'vue'
import Component from 'vue-class-component'
import * as api from '../../../utils/api'
import * as store from '../../../plugins/store/store'
import handledText from '../Text.vue'

@Component({
  props: ['message'],
  components: {
    handledText,
  },
})
export default class TelegramDocumentMessage extends Vue {
  link = ''
  loading = false
  disabled = false

  async loadSticker() {
    this.loading = true
    try {
      let fetched = await api.getFilesLink(this.$props.message)
      switch (fetched.status) {
        case 'ok':
          this.link = fetched.url
          window.open(this.link, '_blank')
          break
        case 'tobig':
          this.disabled = true
          store.setSnackbar({
            message: 'errors.media.tobig',
            color: 'error',
            active: true,
          })
          break
          defult: store.setSnackbar({
            message: 'errors.media.loading',
            color: 'error',
            active: true,
          })
      }
    } catch {
      store.setSnackbar({
        message: 'errors.media.loading',
        color: 'error',
        active: true,
      })
    } finally {
      this.loading = false
    }
  }

  filesize(bytes: number) {
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB']
    if (bytes === 0) return 'n/a'
    const i = Math.floor(Math.log(bytes) / Math.log(1024))
    if (i === 0) return `${bytes} ${sizes[i]}`
    return `${(bytes / 1024 ** i).toFixed(1)} ${sizes[i]}`
  }

  mobile() {
    if (this.$vuetify.breakpoint.xsOnly) {
      return 'center'
    }
    return 'start'
  }
}
