












































































































































































































































import Vue from 'vue'
import Component from 'vue-class-component'
import AddBotDialog from './dialogs/AddBotDialog.vue'
import EditBotTab from './dialogs/EditBotTab.vue'
import AddAdminTab from './dialogs/AddAdminTab.vue'
import * as api from '../utils/api'
import { Bot } from '../models/bot'
import * as store from '../plugins/store/store'
import { i18n } from '../plugins/i18n'

@Component({
  props: {
    dialog: Boolean,
    close: Function,
  },
  components: {
    AddBotDialog,
    EditBotTab,
    AddAdminTab,
  },
})
export default class BotsDialog extends Vue {
  loading = false

  addBotDialog = false

  invite: any

  botId = ''
  greetingMessage = ''
  openedTab = ''

  settingsNav = false

  openedBot: Bot | null = null

  mounted() {
    this.updateList()
  }

  get curbot() {
    if (!this.bot) {
      return null
    }
    for (const bot of store.bots()) {
      if (bot._id === this.bot._id) {
        return bot
      }
    }
  }

  setDefault() {
    this.openedTab = ''
    this.openedBot = null
    this.invite = null
  }

  get mobile() {
    return this.$vuetify.breakpoint.xsOnly
  }

  get bot() {
    return this.openedBot
  }

  get tab() {
    return this.openedTab
  }

  openTab(bot: any, tab: string) {
    if (tab !== 'invite') {
      this.openedBot = bot
      this.openedTab = tab
    } else {
      this.openedTab = tab
      this.invite = bot
      this.openedBot = null
    }
  }

  ownershipStatus(bot: Bot) {
    if (bot && !bot.administrated) {
      return i18n.t('yes')
    }
    return i18n.t('no')
  }

  closeAddBotDialog() {
    this.addBotDialog = false
  }

  getIcon(type: string) {
    switch (type) {
      case 'telegram':
        return 'mdi-telegram'
        break
      case 'viber':
        return 'mdi-phone-in-talk'
        break
      case 'vk':
        return 'mdi-vk'
        break
      default:
        return
    }
  }

  async updateList() {
    if (!store.user()) {
      return
    }
    this.loading = true
    try {
      store.setBots(await api.getBots())
      const user = store.user()
      if (!user) {
        return
      }
      const { status, nextInvoice } = await api.getSubscriptionStatus()
      user.subscriptionStatus = status
      user.nextInvoice = nextInvoice
      store.setUser(user)
    } catch (err) {
      store.setSnackbarError(err.message)
    } finally {
      this.loading = false
    }
  }

  async updateBot(bot: Bot) {
    this.loading = true
    try {
      await api.updateBot(bot)
      store.setBots(await api.getBots())
    } catch (err) {
      store.setSnackbarError(err.message)
    } finally {
      this.loading = false
    }
  }

  async rejectBot(bot: Bot) {
    if (!confirm(i18n.t('rejectBot.confirm', { name: bot.name }) as string)) {
      return
    }
    this.loading = true
    try {
      await api.RejectBot(bot._id)
      store.setBots(await api.getBots())
    } catch (err) {
      store.setSnackbarError(err.message)
    } finally {
      this.setDefault()
      this.loading = false
    }
  }

  async RejectInvite(inviteID: string) {
    this.loading = true
    try {
      await api.RejectInvite(inviteID)
      store.setBots(await api.getBots())
    } catch (err) {
      store.setSnackbarError(err.message)
    } finally {
      this.loading = false
    }
  }

  async AcceptInvite(inviteID: string) {
    this.loading = true
    try {
      await api.AcceptInvite(inviteID)
      store.setBots(await api.getBots())
    } catch (err) {
      store.setSnackbarError(err.message)
    } finally {
      this.loading = false
    }
  }

  async deleteBot(bot: Bot) {
    if (!confirm(i18n.t('deleteBot.confirm', { name: bot.name }) as string)) {
      return
    }

    this.loading = true
    try {
      await api.deleteBot(bot)
      store.setBots(await api.getBots())
    } catch (err) {
      store.setSnackbarError(err.message)
    } finally {
      this.setDefault()
      this.loading = false
    }
  }
}
