















































































import Vue from 'vue'
import Component from 'vue-class-component'
import AddBotDialog from './AddBotDialog.vue'
import * as api from '../utils/api'
import * as store from '../plugins/store/store'
import { i18n } from '../plugins/i18n'
import moment from 'moment'

@Component({
  props: ['dialog', 'close'],
  watch: {
    dialog: function (val) {
      if (val) {
        ;(this as any).updateStatus()
      }
    },
  },
})
export default class Checkout extends Vue {
  loading = false
  nextInvoice: any
  async subscripe() {
    try {
      this.loading = true
      const session = (await api.getStripeSessionId()) as any
      const stripe = (window as any).Stripe(process.env.VUE_APP_STRIPE_PK)
      await stripe.redirectToCheckout({ sessionId: session.id })
    } catch (e) {
      console.log(e)
    }
  }

  formatDate(amount: any) {
    return moment(amount * 1000).format('DD.MM.YYYY')
  }

  async updateStatus() {
    this.loading = true
    const user = store.user()
    if (!user) {
      this.loading = false
      return
    }
    const { status, nextInvoice } = await api.getSubscriptionStatus()
    user.subscriptionStatus = status
    user.nextInvoice = nextInvoice
    this.nextInvoice = nextInvoice
    store.setUser(user)
    this.loading = false
  }

  mounted() {
    this.updateStatus()
  }

  get user() {
    return this.$store.state.user
  }

  async cancelSubscription() {
    try {
      this.loading = true
      await api.cancelSubscription()
      const user = store.user()
      if (!user) {
        return
      }
      const { status, nextInvoice } = await api.getSubscriptionStatus()
      user.subscriptionStatus = status
      user.nextInvoice = nextInvoice
      this.nextInvoice = nextInvoice
      store.setUser(user)
    } catch (e) {
      console.log(e)
    } finally {
      this.loading = false
    }
  }
}
