



















import Vue from 'vue'
import Component from 'vue-class-component'
import * as api from '../../../utils/api'
import * as store from '../../../plugins/store/store'
import handledText from '../Text.vue'

@Component({
  props: ['message'],
  components: {
    handledText,
  },
})
export default class TelegramVideoMessage extends Vue {
  link = ''
  loading = false
  opened = false

  updated() {
    let player: any
    player = this.$refs.videoplayer
    player.pause()
  }

  async loadVideo() {
    this.loading = true
    try {
      let fetched = await api.getFilesLink(this.$props.message)
      switch (fetched.status) {
        case 'ok':
          this.link = fetched.url
          this.opened = true
          break
        case 'tobig':
          store.setSnackbar({
            message: 'errors.media.tobig',
            color: 'error',
            active: true,
          })
          break
          defult: store.setSnackbar({
            message: 'errors.media.loading',
            color: 'error',
            active: true,
          })
      }
    } catch {
      store.setSnackbar({
        message: 'errors.media.loading',
        color: 'error',
        active: true,
      })
    } finally {
      this.loading = false
    }
  }
}
