























































import Vue from 'vue'
import Component from 'vue-class-component'
import * as store from '../plugins/store/store'
import { i18n } from '../plugins/i18n'
import * as api from '../utils/api'
import BotsDialog from './BotsDialog.vue'
import Checkout from './Checkout.vue'
import Merge from './Merge.vue'
import { Watch } from 'vue-property-decorator'

@Component({
  components: {
    BotsDialog,
    Checkout,
    Merge,
  },
})
export default class Navbar extends Vue {
  botsDialog = false
  subscriptionDialog = false
  mergeDialog = false

  get mobileChatOpened() {
    if (!this.$vuetify.breakpoint.smAndDown) {
      return true
    }
    return !store.activeChat()
  }

  get locales() {
    return [
      { icon: '🇺🇸', code: 'en' },
      { icon: '🇷🇺', code: 'ru' },
    ]
  }
  get currentLocale() {
    for (const locale of this.locales) {
      if (locale.code === i18n.locale) {
        return locale
      }
    }
  }

  get currentTheme() {
    if (store.dark()) {
      return `${i18n.t('themes.dark')}: ${i18n.t('themes.on')}`
    }
    return `${i18n.t('themes.dark')}: ${i18n.t('themes.off')}`
  }

  get sdfdsfds() {
    return store.activeChat()
  }

  mounted() {
    ;(this.$vuetify.theme as any).dark = store.dark()
  }

  closeBotsDialog() {
    this.botsDialog = false
  }
  closeSubscription() {
    this.subscriptionDialog = false
  }
  closeMerge() {
    this.mergeDialog = false
  }
  toggleMode() {
    store.setDark(!store.dark())
    ;(this.$vuetify.theme as any).dark = store.dark()
  }
  changeLanguage(locale: string) {
    i18n.locale = locale
    store.setLanguage(locale)
    document.title = i18n.t('title') as string
  }
  logout() {
    store.logout()
    this.$router.replace('/')
  }
}
